import React from 'react';
import styled from 'styled-components';
import { StaticQuery, graphql } from 'gatsby';
import Img from 'gatsby-image';

import { Section, Container } from '@components/global';
import SectionHeader from '@ui/sectionheader';


const SPECS = [
  {
    name: '6+ hours of wireless playtime.',
    image: 'icon-01.png',
    role: 'Cord-free listening has never been this liberating. With 30+ hours of battery life, you can listen for days on a single charge.',
  },
  {
    name: 'Crisp, full- spectrum sound.',
    image: 'icon-02.png',
    role: 'Enjoy your music at its full potential. Designed for more precise acoustical tuning and a wider-frequency response, Plattan 2 Bluetooth produces a crisp, natural, an immersive sound experience.',
  },
  {
    name: 'Easy music navigation.',
    image: 'icon-03.png',
    role: 'Navigate your music with ease using a single, intuitive control wheel. Skip tracks, adjust volume, and pick up calls on the go.',
  },
  {
    name: 'Take them everywhere.',
    image: 'icon-04.png',
    role: 'A convenient, collapsible design makes it easy to take your headphones wherever you go. Simply fold them up and be on your merry way.',
  },
  {
    name: 'Listen to your music free from cords.',
    image: 'icon-05.png',
    role: 'Enjoy your music free from cords. Bluetooth technology lets you easily connect with your sound source for hours of wireless listening, with up to 10 meters of listening range.',
  },
];



const TechSpec = () => (
  <StaticQuery
    query={graphql`
      query {
        allFile(filter: { sourceInstanceName: { eq: "spec" } }) {
          edges {
            node {
              relativePath
              childImageSharp {
                fluid(maxWidth: 400, maxHeight: 400) {
                  ...GatsbyImageSharpFluid
                }
              }
            }
          }
        }
        art_spec: file(
          sourceInstanceName: { eq: "art" }
          name: { eq: "bw_flat" }
        ) {
          childImageSharp {
            fluid(maxWidth: 1600) {
              ...GatsbyImageSharpFluid_withWebp_tracedSVG
            }
          }
        }
      }
    `}
    render={data => (
      <Section id="TechSpec" accent="secondary">
        <SectionHeader header="What Zombies Can Teach You About Hearing Aids" subheader="Get outta your head, get into your heart " />
        <Container style={{ position: 'relative' }}>
          <Grid>
            <div>
              <h3>Key Feature</h3>
              <p>
                The world's most primitive people have few possessions. but they are not poor. Poverty is not a certain small amount of goods, nor is it just a relation between means and ends; above all it is a relation between people. Poverty is a social status. As such it is the invention of civilisation. It has grown with civilisation, at once as an invidious distinction between classes and more importantly as a tributary relation that can render agrarian peasants more susceptible to natural catastrophes than any winter camp of Alaskan Eskimo.
              </p>
            </div>
            <SpecGrid>
              {SPECS.map(({ name, image, role }) => {
                const img = data.allFile.edges.find(
                  ({ node }) => node.relativePath === image
                ).node;

                return (
                  <SpecItem>
                    <Art>
                      <Img fluid={img.childImageSharp.fluid} alt={name} />
                    </Art>
                    <div>
                      <Title>{name}</Title>
                      <p>{role}</p>
                    </div>
                  </SpecItem>
                );
              })}
            </SpecGrid>
          </Grid>
        </Container>
      </Section>
    )}
  />
);


const Grid = styled.div`
  display: grid;
  grid-template-columns: 1fr 2fr;
  grid-gap: 50px;
  text-align: left;
  align-items: top;
  justify-items: left;

  @media (max-width: ${props => props.theme.screen.md}) {
    grid-template-columns: 1fr;
    text-align: left;
    margin-bottom: 96px;

    &:last-child {
      margin-bottom: 24px;
    }
  }
`;

const SpecGrid = styled.div`
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: min-content;
  grid-gap: 30px;
  justify-content: space-between;
  width: 100%;
  margin-top: 32px;

  @media (max-width: ${props => props.theme.screen.lg}) {
    justify-content: start;
  }

  @media (max-width: ${props => props.theme.screen.md}) {
    width: 100%;
    grid-template-columns: repeat(auto-fill, minmax(160px, 1fr));
  }

  @media (max-width: ${props => props.theme.screen.xs}) {
    grid-gap: 24px;
  }
`;

const SpecItem = styled.div`
  display: grid;
  grid-template-columns: 1fr 3fr;
  grid-gap: 30px;
  text-align: left;
  align-items: top;
  justify-items: left;

  @media (max-width: ${props => props.theme.screen.md}) {
    grid-template-columns: 1fr;
    text-align: left;
    margin-bottom: 96px;

    &:last-child {
      margin-bottom: 24px;
    }
  }
`;

const Art = styled.figure`
  margin: 0;
  max-width: 1380px;
  width: 100%;
  img{
    width: 100%;
  }
`;

const Title = styled.p`
  margin-bottom: 10px;
  color: ${props => props.theme.color.black.regular};
`;

export default TechSpec;
